<template>
  <div class="achievementContent">
    <div class="undercarriageMask"  v-if="undercarriageMask">
      <div class="undercarriageContent">
        <p>
          选择对象
          <i class="el-icon-close" @click="close()"></i>
        </p>

        <div class="undercarriageText">
          确定{{state == 0 ? '下架' : '上架'}}该成果发布吗
        </div>

        <div class="undercarriageButton">
          <span class="cancel" @click="close()">取消</span>
          <span class="sure" @click="sure()">确定</span>
        </div>
      </div>  
    </div>

  	<div class="achievementTitle">
  	  成果审核
  	</div>
  	<div class="requirement">
      <span>
        状态
        <el-select :clearable="clearable" v-model="value1" placeholder="请选择" class="selected" @change="selectStatus">
  	      <el-option   
  	       v-for="item in options1"
  	       :key="item.value1"
  	       :label="item.label1"
  	       :value="item.value1">
  	      </el-option>
  	    </el-select>
      </span>

      <span>
        类别
        <el-cascader
          :options="options"
           v-model="value"
           @change="selectType"
           placeholder="不限"
           @expand-change="selectType"
        >
        </el-cascader>
      </span>

      <span>
        来源
        <el-select v-model="value2" placeholder="请选择" class="selected" @change="selectFrom">
	      <el-option
	       v-for="item in options2"
	       :key="item.value2"
	       :label="item.label2"
	       :value="item.value2">
	      </el-option>
	    </el-select>
      </span>

      <span>
        <el-date-picker
          v-model="value3"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          @change="selectTime"
          value-format="timestamp"
          end-placeholder="结束日期">
        </el-date-picker>
      </span>

      <span class="search">
      	<i class="el-icon-search"></i>
      	<el-input v-model="search" placeholder="请输入搜索内容" class="inputSearch"></el-input>
      	<el-button type="primary" class="searchButton" @click="inputSearch">搜索</el-button>
      </span>

      <span class="clear" @click="clear">
        清除
      </span>
    </div>

    <div class="searchResult">
    	<ul class="tableTr">
        <li>成果名称</li>
        <li>发布人</li>
        <li>类别</li>
        <li>来源</li>
        <li>状态</li>
        <li>发布时间</li>
        <li class="operate">操作</li>
    	</ul>

      <div class="noData" v-if="noData">
        暂无数据
      </div>

    	<div class="list">
    		<ul class="tableTr" 
          v-for="(item, index) in optionArr" :key="index"
          >
	        <li>
            <span class="title">{{item.blogTitle}}</span>
          </li>
	        <li>{{item.userNickname}}</li>
	        <li>{{item.tagTypeName2}}</li>
	        <li>
            {{
              item.msgFrom == 1 ? "自主研发" : "" 
              || 
              item.msgFrom == 2 ? "合作研发" : "" 
              || 
              item.msgFrom == 3 ? "企业研发":""
            }}
          </li>
	        <li>
            <span class="draft" v-if="item.auditState == 0">
              草稿
            </span>
            <span class="noExamine" v-if="item.auditState == 1">
              未审核
            </span>
            <span class="passed" v-if="item.auditState == 2">
              被通过
            </span>
            <span class="reject" v-if="item.auditState == 3">
              被驳回
            </span>
          </li>
	        <li>
            {{formatDate(item.createTime)}}
          </li>
	        <li class="operate">
	        	<span class="pass" 
              @click="passReject(item.blogId,2)" 
              v-if="item.auditState != 2 && item.auditState != 3"
            >
              通过
            </span>
	        	<span 
              class="reject" 
              v-if="item.auditState != 2 && item.auditState != 3"
              @click="rejectReasonMask(item.blogId)" 
            >
             驳回
            </span>
            <span 
              class="undercarriage" 
              @click="undercarriage(item.blogId, item.isUpperShelf == false ? 1 : 0)" 
              v-if="item.auditState == 2"
              :class="{isundercarriage : item.isUpperShelf == true}"
            >
             {{item.isUpperShelf == false ? "上架" :  "下架"}}
            </span>
	        	<span 
              class="delete" 
              @click="deleteAudit(item.blogId, index, pageNum, false)"
            >
             删除
            </span>
	        	<span 
              class="detail"
              @click="details(item.blogId)">
	        		详情
	        		<i class="el-icon-arrow-right"></i>
	        	</span>
	        </li>
	    	</ul>
    	</div>
    </div>
    <el-dialog
      title="微科技"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <div class="dialogContent">
        <p>请选择驳回原因</p>
        <div class="rejectReason">
          <el-radio-group v-model="radio2">
            <el-radio label="涉及敏感内容">涉及敏感内容</el-radio>
            <el-radio label="内容描述不清">内容描述不清</el-radio>
            <el-radio label="展示效果不强">展示效果不强</el-radio>
            <el-radio label="4">
              其他
              <el-input
               v-model="anothorResultInput"  
               class="anothorResult" 
               placeholder="请输入内容"
              ></el-input>
            </el-radio>
          </el-radio-group>

        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="passReject(blogId,3)">确 定</el-button>
      </span>
    </el-dialog>
    <div class="page">
	    <el-pagination
			  background
			  layout="prev, pager, next"
			  :total="total"
        @current-change="handleCurrentChange"
        >
			</el-pagination>
		</div>
  </div>
</template>
<style lang="less" scoped>
  .achievementContent{
  	margin:60px 0 60px 260px;
    .dialogContent{
      .rejectReason{
        margin-top: 25px;
        .el-radio{
          display: block;
          margin-bottom: 20px;
        }
      }
    }
    .delete{

    }
    .anothorResult{
      margin-left: 15px;
    }
    .undercarriageMask{
      position: fixed;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 555555;
      background: rgba(0,0,0,0.4);
      .undercarriageContent{
        width: 430px;
        height: 190px;
        border-radius: 5px;
        background: white;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-70%);
        overflow: hidden;
        >p{
          background: rgb(233,233,233);
          margin:0;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          text-align: left;
          padding-left: 40px;
          position: relative;
          .el-icon-close{
            position: absolute;
            right: 10px;
            top: 10px;
            font-size: 20px;
            color: #adacac;
            cursor: pointer;
          }
        }
        .undercarriageText{
          height: 100px;
          line-height: 100px;
          font-size: 16px;
          color: #7e7e7e;
        }
        .undercarriageButton{
          text-align: right;
          span{
            display: inline-block;
            width: 70px;
            height: 24px;
            text-align: center;
            line-height: 24px;
            border-radius: 3px;
            cursor: pointer;
          }
          .cancel{
            background: rgb(233,233,233);
            color: #7e7e7e;
          }
          .sure{
            background: rgb(76,180,231);
            margin:0 25px;
            color: white;
          }
        }
      }
    }
  	.achievementTitle{
  	  color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  text-align: left;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
  	}
  	.requirement{
  	  text-align: left;
  	  font-size: 12px;
  	  padding:20px 50px;
  	  span{
  	  	margin-right: 20px;
  	  	display: inline-block;
  	  	margin-bottom: 20px;
  	  	.selected{
  	  	  margin-left: 8px;
  	  	  width: 170px;
  	  	}
  	  }
      .clear {
        font-size: 14px;
        display: inline-block;
        width: 60px;
        height: 38px;
        border: 1px solid #409EFF;
        color: #409EFF;
        text-align: center;
        line-height: 38px;
        border-radius: 3px;
        cursor: pointer;
      }
  	  .search{
  	  	.el-icon-search{
  	  		position: relative;
  	  		left: 26px;
  	  		z-index: 2;
  	  	}
        .inputSearch{
          width: auto;
          left: 3px;
        }
  	  	.searchButton{
  	  		font-size: 14px;
  	  	}
  	  }
  	}
  	.searchResult{
  		padding: 0px 50px;
      .noData{
        padding : 1.5rem;
      }
  		.tableTr{
  			margin:0;
  			display: flex;
  			background: rgb(216,216,216);
  			li{
  				list-style-type: none;
  				flex: 1;
  				height: 50px;
  				line-height: 50px;
  				font-size: 14px;
          width: 120px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          padding: 0 15px;
          .draft{
            color: #8f8fff;
          }
          .noExamine{
            color: rgb(7,210,210);
          }
          .passed{
            color: rgb(81,223,66);
          }
          .reject{
            color: rgb(217,72,52);
          }
  			}
  			.operate{
  				flex: 2;
  			}
  		}
  		.list{
  			.tableTr{  				
  				li{
  					font-size: 12px;
  					position: relative;
  				}
          .operate{
          	flex: 2;
          	span{
          		display: inline-block;
          		margin: 0 5px;
          		color: red;
          		font-weight: bold;
          		cursor: pointer;
          	}
          	.pass{
          		background: rgb(76,180,231);
          		height: 24px;
          		line-height: 24px;
          		width: 40px;
          		border-radius: 4px;
          		color: white;
          	}
          	.detail{
          		color: #a5a5a5;
          		float: right;
              position: relative;
              right: 5%;
          	}
          	.undercarriage{
          		background: rgb(227,168,31);
          		height: 24px;
          		line-height: 24px;
          		width: 40px;
          		border-radius: 4px;
          		color: white
          	}
            .isundercarriage{
              background: #409EFF;
            }
          }
  			}
  			.tableTr:nth-child(2n+1){
  				background: rgb(241,246,254);
  			}
  			.tableTr:nth-child(2n){
  				background: white;
  			}
  		}
  	}
  	.page{
  		margin-top: 50px;
  	}
  }
</style>
<script>
import {mapState} from 'vuex';
//状态的为options1  ， 类别为options
  export default {
    data() {
      return {
        cityDefault:'',
        options1: [{
          value1: -1,
          label1: '不限'
        },{
          value1: 0,
          label1: '草稿'
        },{
          value1: 1,
          label1: '未审核'
        },{
          value1: 2,
          label1: '被通过'
        },{
          value1: 3,
          label1: '被驳回'
        }],
        value1: '不限',

        options:[{
          value: [],
          label: '不限',
          children:[{
            value: [],
            label: '不限'
          }]
        }],
        value: "",
        options2: [{
          value2: -1,
          label2: '不限'
        },{
          value2: 1,
          label2: '自主研发'
        },{
          value2: 2,
          label2: '合作研发'
        },{
          value2: 3,
          label2: '企业研发'
        }],
        value2: '不限',
        value3: [],
        search: '',
        undercarriageMask: false,
        optionArr: [],
        total: 0,
        noData: false,
        blogId: "",
        state: 0 ,//将作品下架，状态码为0，反之为1
        pageNum: 1,
        dialogVisible: false,
        radio2: '涉及敏感内容',
        anothorResultInput: '',
        rejectInfo: '',
        clearable: true,
        selectTypeVal:''
      }
    },
    computed: {
      ...mapState({
        userInfo:(state)=>state.mutations.userInfo
      })
    },
    created() {
      this.updateToken()
      this.getCategory()
    },
    methods:{
      //判断管理员权限
      judegeIsAdmin(){
        if(this.userInfo.type == 2){
          this.cityDefault = this.userInfo.district
          console.log('变更省份1:',this.cityDefault)
          this.getAuditBlog()
        }else{
          this.getAuditBlog()
        }
      },
      //刷新token
      updateToken() {
        let accessToken = localStorage.getItem("accessToken")
        if (accessToken) {
          let url =this.api.configIp + this.net.updateToken;
          let params = {
            accessToken: accessToken
          }
          this.$fetch(url,params).then((res)=>{
            console.log('sucess:',typeof(res.data))
            if(res.success){
              this.$store.commit("saveUserInfo",JSON.parse(res.data))
              this.judegeIsAdmin()
            }else{
              this.judegeIsAdmin()
            }
          }).catch((err)=>{
            console.log('err:',err)
          })
        }
      },
    	details(blogId) {
    		this.$router.push({
         	path: `/achievementAudit/details`,
          query: {blogId: blogId}
        })
    	},
      undercarriage(blogId, state) {
        this.blogId = blogId;
        //上下架的状态码
        this.state = state;
        this.undercarriageMask = true;
      },
      close() {
        this.undercarriageMask = false;
      },
      getAuditBlog(state, tagTypeId, msgFrom, time, inputSearch, pageNum) {
        let pageNum1 = !pageNum ? 1 : pageNum;
        if(typeof(state) == "string" || state == -1){
          state = "";
        }

        if(!tagTypeId){
          tagTypeId = ["", ""];
        }

        if(tagTypeId.join("") == ""){
          tagTypeId = ["",""];
        }


        if(typeof(msgFrom) == "string" || msgFrom == -1){
          msgFrom = "";
        }
        
        if(!time){
          time = ["",""]
        }
        
        let params = {
          auditTerminal:'1',
          pageNum  : pageNum1,
          pageSize : 10,
          auditState: state,
          tagTypeId1:tagTypeId[0],
          tagTypeId2:tagTypeId[1],
          msgFrom: msgFrom,
          startDate: time[0],
          endDate: time[1],
          blogTitle: inputSearch,
          orderByParam:"{auditState:1}",
          district:this.cityDefault
        };

        this.$post(`${this.url}/manage/audit/getAllAuditBlog`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            this.noData = response.data.result.length == 0 ? true : false;
        
            this.optionArr = response.data.result;
            this.total = response.data.total;
            this.pageNum = response.data.pageNum;
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      selectStatus(val) {
        this.getAuditBlog(val, this.value, this.value2, this.value3,this.search);
      },
      selectFrom(val) {
        this.getAuditBlog(this.value1, this.value, val, this.value3,this.search);
      },
      selectTime(val) {
        this.getAuditBlog(this.value1, this.value, this.value2, val,this.search);
      },
      selectType(val) {
        this.selectTypeVal = val;
        this.getAuditBlog(this.value1, val,this.value2, this.value3,this.search);
      },
      inputSearch() {
        this.getAuditBlog(this.value1, this.value,this.value2, this.value3 , this.search);
      },
      handleClose() {
        this.dialogVisible = false;
      },
      passReject(blogId,state) {

        let rejectInfo = this.radio2 == 4 ? this.anothorResultInput : this.radio2;
        let userId = this.userInfo.id;
        let isUpperShelf;

        if(state == 2){
          rejectInfo = "";
          isUpperShelf = true
        }else{
          isUpperShelf = false;
        }

        let params = {
          blogId  : blogId,
          auditUserId : userId,
          auditState: state,
          rejectInfo: rejectInfo,
          isUpperShelf: isUpperShelf
        };

        this.$post(`${this.url}/manage/audit/auditAch`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            let passReject = state == 3 ? "驳回成功" : "审核通过";
            this.$message({
              message: passReject,
              type: 'success'
            });

            this.getAuditBlog(this.value1, this.value,this.value2, this.value3, this.search);
            this.dialogVisible = false;
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      sure() {
        let blogId = this.blogId;

        let isUpperShelf = this.state == 0 ? false : true;
        let params = {
          blogId  : blogId,
          isUpperShelf : isUpperShelf,
        };

        this.$post(`${this.url}/manage/audit/editShelfAch`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            this.$message({
              message: `${this.state == 0 ? '下架' : '上架'}成功`,
              type: 'success'
            });
            this.undercarriageMask = false;
            this.getAuditBlog(this.value1, this.value,this.value2, this.value3 , this.search);
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      deleteAudit(blogId, index, pageNum,isDelete) {
        let params = {
          blogId  : blogId,
          isDelete : isDelete
        };

        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteBlog(blogId, index, pageNum,isDelete,params);

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      deleteBlog(blogId, index, pageNum,isDelete,params){
        this.$post(`${this.url}/manage/audit/removeAch`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getAuditBlog(this.value1, this.value,this.value2, this.value3 , this.search, pageNum);
          }else if(response.code == 610){
            this.$confirm('该成果正在展示中，是否确定删除?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              let params = {
                blogId  : blogId,
                isDelete : true
              };
              this.deleteBlog(blogId, index, pageNum,true, params)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });          
            });
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      clear() {
        this.search = '';
        this.value1 = -1;
        this.value = '';
        this.value2 = '不限';
        this.value3 = '';
        this.getAuditBlog(this.value1, this.value,this.value2, this.value3 , this.search);
      },
      rejectReasonMask(blogId) {
        this.dialogVisible = true;
        this.blogId = blogId;
      },
      getCategory() {
        this.$post(`${this.url}/manage/category/getAllCategory`,
          {}
        )
        .then((response) => {
          if(response.code == 200){
            let data = response.data;
           
            const newTeam = function (data, parentId) {
              let itemArr = [];
              for (let i = 0; i < data.length; i++) {
                let node = data[i];

                if (node.parentCategoryId == parentId) {
                  let newNode = {};
                  newNode.value = node.categoryId;
                  newNode.label = node.categoryName;
                  itemArr.push(newNode);
                  if(newTeam(data, node.categoryId).length != 0){
                    newNode.children = newTeam(data, node.categoryId);     
                  }            
                }
              }
              return itemArr;              
            }
            
            this.options = [...this.options, ...newTeam(data, 0)];

          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      handleCurrentChange(val) {
        this.getAuditBlog(this.value1, this.selectTypeVal,this.value2, this.value3 , this.search, val)
      }
    },
    
  }
</script>


